.skill {

    margin-top: 20px;
    margin-bottom: 20px;
    padding: 40px;
    transition: transform 0.4s cubic-bezier(0.075, 0.82, 0.165, 1);
    min-height: calc(100vh/4);
}

.skill:hover{
    cursor: default;
    transform: scale(1.1, 1.1);;
    border: 1px solid white;
}

.skill p {
    /* border: 1px solid white; */
    margin: 20px 0px 10px 2px;
    display: inline-block;
    font-size: 30px;
    font-family: 'Do Hyeon', sans-serif;
    color: white;
}

.base-bar {
    width: 80vw;
    height: 8vh;
    margin: 0;
    border: 1px solid rgb(30, 32, 32);
    background-image:
        linear-gradient(to bottom, rgba(255, 209, 236, 0.4) 0%, rgba(255, 209, 236, 0) 50%, rgba(255, 209, 236, 0.4) 100%),
        linear-gradient(to right, rgba(255, 209, 236, 0.4) 0%, rgba(255, 209, 236, 0) 20%),
        linear-gradient(to right, rgb(101, 248, 192) 0%, rgb(0, 255, 13) 100%);
}   

.percent-bar {
    float: right;
    background-image: 
        linear-gradient(to bottom, rgba(255, 255, 255, 0.05) 0%, rgba(0, 0, 0, 0.05) 50%, rgba(255, 255, 255, 0.05) 100%),
        linear-gradient(to right, rgb(39, 44, 43) 0%, rgb(45, 42, 46) 0%);
    
}


.current-percent-bar {
    background:linear-gradient(to right, rgba(255, 255, 255, 0) 0%, rgba(216, 199, 199, 0.5) 50%, rgba(255, 255, 255, 0) 100%);
}